canvas {
    /* height:  80%;
    width: 80%;  */
    margin: 20px;
    border-radius: 5%;
}

@media (max-aspect-ratio: 1/1){
    canvas {
        width: 80%;
    }
}

@media (min-aspect-ratio: 1/1){
    canvas {
        height: 80%;
    
    }
}

#canvas-container {
    display: flex;
    
    flex-grow: 1;
    justify-content: center;
    align-items: center;
}
