.App {
  text-align: center;
  background-color: rgb(35, 35, 35);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #100155;
  /* min-height: 10vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: white;
}

form {
  border: solid 1px gainsboro;
  border-radius: 5px;
  /* width: 90%; */
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 10px;
  padding: 10px 0;
}

@media screen and (max-width: 600px) {
  form {
    justify-content: center;
  }
}